import { auth as authApi } from "../../api";
import actions from "./actions";

var signUpUserWithEmailAndPassword = function signUpUserWithEmailAndPassword(email, password) {
  return actions.signIn(authApi.signUpUserWithEmailAndPassword(email, password));
};

var signInWithEmailAndPassword = function signInWithEmailAndPassword(email, password) {
  return actions.signIn(authApi.signInWithEmailAndPassword(email, password));
};

var signOut = function signOut() {
  return actions.signOut(authApi.signOut());
};

var listenChangesCallback = null;

var startListenAuthChanges = function startListenAuthChanges() {
  return function (dispatch) {
    if (!listenChangesCallback) {
      return new Promise(function (resolve, reject) {
        var executedBefore = false;

        listenChangesCallback = function listenChangesCallback(authUser) {
          if (authUser) {
            dispatch(actions.signInFulfilled(authUser));
          } else {
            dispatch(actions.signInRejected(null));
          }

          if (!executedBefore) {
            executedBefore = true;
            resolve(authUser);
          }
        };

        authApi.startListenAuthChanges(listenChangesCallback, function (error) {
          return reject(error);
        });
      });
    }

    return Promise.reject("Listen alread started");
  };
};

var stopListenAuthChanges = function stopListenAuthChanges() {
  return function () {
    if (listenChangesCallback) {
      authApi.stopListenAuthChanges(listenChangesCallback);
      listenChangesCallback = null;
    }
  };
};

export default {
  signUpUserWithEmailAndPassword: signUpUserWithEmailAndPassword,
  signInWithEmailAndPassword: signInWithEmailAndPassword,
  signOut: signOut,
  startListenAuthChanges: startListenAuthChanges,
  stopListenAuthChanges: stopListenAuthChanges
};